@import "src/assets/sass/variables";

.personalBox {
  padding-bottom: 50px;
  position: relative;


  .personalInfo {
    .personalInfoTitle {
      display: flex;

      .personalIcon {
        padding-right: 10px;
        font-size: 16px;
      }

      .personalInfoHeading {
        margin: 0 0 12px;
        font-size: 20px;
        line-height: 110%;
        font-weight: 800;
        text-transform: uppercase;
      }
    }

    .personalDesc {
      width: 80%;
      color: $secondaryText;
      font-size: 15px;
      line-height: 2;
    }

    .personalInfoInner {
      width: 80%;
      display: flex;
      margin: 0;

      .personalInfoList {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        list-style: none;
        padding: 0;
        height: 130px;
        color: $secondaryText;
        margin: 0 0 30px;
        width: 100%;

        .personalInfoItem {
          display: flex;
          padding-bottom: 13px;
          font-weight: 700;
        }
      }

    }
  }

  .resume {
    display: flex;
    justify-content: center;
    align-content: center;
    background-color: $bgFourth;
    border: 1px solid $line;
    width: 250px;

    .resumeLink {
      display: flex;
      justify-content: center;
      align-items: center;
      text-decoration: none;
      font-size: 15px;
      height: 40px;
      font-weight: 700;
      text-align: center;
      letter-spacing: .5px;
      color: $accentText;
      text-transform: uppercase;
      cursor: pointer;
    }
  }
}

.personalBox::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 1px;
  background-color: $line;
  bottom: 0;
}