.wrapper {
  overflow: hidden;
}

.rightBox {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  /*width: 66.6666666667%;*/
  width: 65%;
  left: 33.3333333333% !important;
  padding: 0;
  margin: 0;
  top: 0;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  justify-content: space-between;
  margin-top: 24px;
  height: calc(100vh - 48px);
}


.container {
  margin: 0 auto;
  max-width: 1280px;
  width: 100%;
  padding-bottom: 50px;
}


.linkBack {
  position: absolute;
  top: 32px;
  right: 48px;
  width: 30px;
  cursor: pointer;
  z-index: 100;
}